@import url(https://fonts.googleapis.com/css?family=Lato);
#minor_horizontal_lines,
#minor_vertical_lines {
  stroke-width: 0.25;
}
#major_horizontal_lines,
#major_vertical_lines {
  stroke-width: 1;
}

#axes {
  stroke-width: 3;
}

#bounds {
  stroke-width: 3;
}




#graph {
  width: 100%;
  height: 100%;
}
#board,
#board * {
  -webkit-transition: opacity var(--fast);
  transition: opacity var(--fast);
}
#board[data-edit="true"] {
  opacity: 0;
}
#graph * {
  -webkit-transition: opacity var(--fast);
  transition: opacity var(--fast);
}

*[data-hitbox="true"] {
  opacity: 0;
  -webkit-transition: opacity var(--fast);
  transition: opacity var(--fast);
  cursor: pointer;
}
*[data-hitbox="true"]:hover,
*[data-hitbox="true"][data-selected="true"] {
  opacity: 0.5;
}

.tooltip {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  position: fixed;
  padding: 5px;
  background: var(--dark-gray);
  color: var(--white);
  font-size: 0.75em;
  white-space: nowrap;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  pointer-events: none;
  z-index: 99999;
}
@media (hover: none) {
  .tooltip {
    display: none;
  }
}
.tooltip > div {
  padding: 2px;
}
.tooltip > div:not(:first-child) {
  opacity: 0.5;
}
.button {
  width: 40px;
  height: 40px;
  background: var(--black);
  color: var(--white);
  -webkit-transition: background var(--fast), color var(--fast);
  transition: background var(--fast), color var(--fast);
}
.button:hover {
  background: var(--dark-gray);
}
.button[data-color='gray'] {
  color: var(--gray);
}
.button[data-color='white'] {
  color: var(--white);
}
.button[data-color='blue'] {
  color: var(--blue);
}
.button[data-color='yellow'] {
  color: var(--yellow);
}
.thin_button {
  width: 25px;
  height: 40px;
}

#top_panel {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  position: fixed;
  background: var(--black);
  left: 0;
  right: 0;
  top: 0;
  border-bottom: solid 2px var(--dark-gray);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 1;
  -webkit-transition: opacity var(--slow);
  transition: opacity var(--slow);
}
body[data-fullscreen="true"] #top_panel {
  opacity: 0;
}
.undo_redo_menu_item {
  width: 100%;
  text-align: left;
  white-space: nowrap;
}

.side_panel_row {
  display: flex;
  justify-content: center;
  align-items: stretch;
  height: 40px;
}
.side_panel_row > * {
  display: flex;
  justify-content: center;
  align-items: center;
}
.side_panel_row > *:nth-child(1) {
  width: 50px;
}
.side_panel_row > *:nth-child(1) > * {
  color: var(--light-gray);
  height: 50%;
}
.side_panel_row > *:nth-child(2) {
  justify-content: flex-start;
  width: 100px;
}
.side_panel_row > *:nth-child(3) {
  width: 70px;
}

.side_panel_header {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 0 5px 10px;
  color: var(--light-gray);
  text-transform: uppercase;
  letter-spacing: 1px;
  text-align: left;
}

#side_panel {
  position: fixed;
  background: var(--black);
  left: 0;
  top: 50%;
  text-align: right;
  width: 220px;
  max-height: calc(100vh - 160px);
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  border: solid 2px var(--dark-gray);
  border-left: none;
  -webkit-transition: opacity var(--slow), -webkit-transform var(--fast);
  transition: opacity var(--slow), -webkit-transform var(--fast);
  transition: transform var(--fast), opacity var(--slow);
  transition: transform var(--fast), opacity var(--slow), -webkit-transform var(--fast);
}
#side_panel[data-open="false"] {
  -webkit-transform: translate(-180px, -50%);
          transform: translate(-180px, -50%);
}
#side_panel[data-open="false"] > *:not(:first-child) {
  visibility: hidden;
}
body[data-fullscreen="true"] #side_panel {
  opacity: 0;
}

.timecode {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 10px;
  width: 110px;
  height: 40px;
  font-size: 0.75em;
}
.timecode span {
  margin: 0 5px;
}
.timecode span:first-child {
  margin-left: 0;
}

@media (max-width: 700px) {
  .timecode {
    width: calc(100% - 40px * 3);
    height: 40px;
  }
}

.rail_container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-grow: 1;
  padding: 0 10px;
}
.rail {
  background: var(--gray);
  position: relative;
  width: 100%;
  height: 15px;
}
.rail:hover {
  cursor: pointer;
}
.rail_marker {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 50%;
  background: var(--blue);
  pointer-events: none;
}
.keyframe_markers {
  width: 100%;
  height: 15px;
}

#bottom_panel {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  position: fixed;
  background: var(--black);
  left: 0;
  right: 0;
  bottom: 0;
  border-top: solid 2px var(--dark-gray);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  z-index: 1;
  -webkit-transition: opacity var(--slow);
  transition: opacity var(--slow);
}
body[data-fullscreen="true"] #bottom_panel {
  opacity: 0;
}

* {
  font-family: "Lato", sans-serif;

  --black: #202020;
  --dark-gray: #404040;
  --gray: #606060;
  --light-gray: #a0a0a0;
  --white: #ffffff;
  --blue: #42a5f5;
  --yellow: #ffeb3b;

  --slow: 1s ease;
  --fast: 0.25s ease;
}
html,
body {
  margin: 0;
  padding: 0;
  background: var(--black);
  color: var(--white);
}
#root {
  width: 100%;
  height: 100%;
}
small {
  font-size: 0.75em;
  color: var(--light-gray);
}
button {
  border: 0;
  margin: 0;
  padding: 0;
  background: none;
  text-decoration: none;
}
button:hover {
  cursor: pointer;
}
*:focus {
  outline: none;
  box-shadow: inset 0 0 0 1px var(--blue);
}
svg {
  width: 20px;
  height: 20px;
}

